<template>
  <a-layout class="container">
    <!-- 模块标题 -->
    <div class="title"><span> 组件 </span></div>
    <a-divider class="divider"></a-divider>

    <!-- 组件 -->
    <!-- 
      设置 :sort="false" 不允许排序，允许拖曳
      设置 clone 方式为深拷贝，并在深拷贝执行过程中为组件生成随机 id
    -->
    <draggable
      v-model="componentList"
      item-key="id"
      :group="{ name: 'component', pull: 'clone', put: false }"
      :sort="false"
      :clone="(original) => deepClone(original)"
      animation="200"
    >
      <template #item="{ element }">
        <a-button class="item">{{ element.options.cType }} </a-button>
      </template>
    </draggable>

    <!-- 测试用 -->
    <!-- {{ componentList }} -->
  </a-layout>
</template>

<script setup>
import draggable from "vuedraggable";
import { nanoid } from "nanoid";  
import dayjs from 'dayjs';

/*
  schema         组件所属集合
  field          组件所属字段
  name           组件标题
  refill         组件是否为回填字段
  options.type   组件类型
  options.cType  组件类型的中文名字
  options.value  组件绑定的值
*/
const componentList = [
  // 单行文本
  {
    id: 1,
    schema: "",
    field: "",
    name: "标题",
    refill: false,
    options: {
      type: "input",
      cType: "单行文本",
      value: "",
      allowClear: true,
      bordered: true,
      placeholder: '请输入文本',
      showCount: true,
    }
  },
  // 多行文本
  {
    id: 2,
    schema: "",
    field: "",
    name: "标题",
    refill: false,
    options: {
      type: "textarea",
      cType: "多行文本",
      value: "",
      allowClear: true,
      autoSize: {minRows: 2},
      placeholder: "请输入文本",
      showCount: false,
    }
  },
  // 单选框
  {
    id: 3,
    schema: "",
    field: "",
    name: "标题",
    refill: false,
    options: {
      type: "radio",
      cType: "单选框组",
      value: "",
      valueData: [
        {value: '选项1', label: '选项1'},
        {value: '选项2', label: '选项2'}
      ],
    }
  },
  // 下拉选择器
  {
    id: 4,
    schema: "",
    field: "",
    name: "标题",
    refill: false,
    options: {
      type: "select",
      cType: "下拉选择器",
      value: "",
      valueData: [
        { value: '选项1'},
        { value: '选项2'}
      ],
    }
  },
  // 评分
  {
    id: 5,
    schema: "",
    field: "",
    name: "标题",
    refill: false,
    options: {
      type: "rate",
      cType: "评分",
      value: 1,
      limit: 10,
    }
  },
  // 日期选择器
  {
    id: 6,
    schema: "",
    field: "",
    name: "标题",
    refill: false,
    options: {
      type: "datePicker",
      cType: "日期选择器",
      value: dayjs('2020-01-01').format("YYYY-MM-DD"),
    }
  },
  // 文字
  {
    id: 7,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "text",
      cType: "文字",
      value: "文字",
      fontFamily: "Georgia, 'Times New Roman', Times, serif",
      fontSize: "16",
      textType: ""
    }
  },
  // 分割线
  {
    id: 8,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "divider",
      cType: "分割线",
      value: "",
      style: "border-color: #63b4ff; font-family:  Georgia, 'Times New Roman', Times, serif;",
      dashed: true,
      orientation: "",
    }
  },
  // 动态增减表单
  // {

  // }
];

// 深拷贝并生成随机id
function deepClone(original) {
  // 深拷贝
  let newItem = JSON.parse(JSON.stringify(original));
  // 生成随机 id
  newItem.id = nanoid();
  return newItem;
}

</script>

<style scoped>
.container {
  height: 100%;
  background-color: white;
}

.title {
  height: 40px;
  color: black;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.divider {
  margin-top: 0;
  border-color: #1890ff;
}

.item {
  font-size: 16px;
  font-family: 'Courier New', Courier, monospace;
  color: white;
  background-color: #1890ff;
  border-radius: 6px;
  width: 180px;
  margin: 4px;
}
</style>