import { defineStore } from 'pinia'
import { ref } from 'vue'

// 公共组件
export const useSelectedComponentStore = defineStore('selectedComponent', () => {
  const component = ref({
    name: "",
    schema: "",
    field: "",
    refill: false,
    options: {
      type: "",
      cType: "",
      value: ""
    }
  });
  
  return { component };
})