<template>
  <a-layout class="container">
    <!-- 模块标题 -->
    <!-- 渲染区 -->
    <div class="title">
      <a-button @click="allClear" danger>清空</a-button>
      <a-button @click="clear" danger>删除</a-button>
      <a-button class="title-left" @click="undo">撤销</a-button>
      <a-button class="title-right" @click="jsonShow">生成JSON</a-button>
      <a-button @click="jsonInput">导入JSON</a-button>
      <a-button @click="previewShow">预览</a-button>
      <a-button type="primary" @click="publishConfirm">发布表单</a-button>
    </div>
    
    <a-divider class="divider"></a-divider>
  
    <!-- 表单 -->
    <a-form>
      <!-- 表单标题 -->
      <div style="display: flex; justify-content: center;">
        <a-input 
          v-model:value="formTitle"
          :bordered="false"
          class="form-title"
        ></a-input>
      </div>
    
      <a-divider></a-divider>
      
      <!-- 
        设置 height: 100% 方便拖曳（存疑）
        设置 :sort="false" 不允许排序，允许拖曳（去除的话可能有 bug —— 存疑）

        error happens when template have commented elements:
        Item slot must have only one child

        内部也可以使用动态组件 component
        目前使用 v-if v-else-if 判断
      -->
      <draggable
        v-model="componentList"
        item-key="id"
        :group="{ name: 'component' }"
        style="height: 100%"
        animation="200"
      >
          <template #item="{ element }">
            <a-form-item
              :class="selected.id == element.id ? 'item-content active' : 'item-content'"
              @click="() => select(element)"
            >
              <FormGenerator :element="element" />
            </a-form-item>
          </template>
      </draggable>
      
    </a-form>

    <!-- 提示等 -->
    <!-- 生成 JSON 的提示框 -->
    <a-modal 
      v-model:visible="jsonShowVisible" 
      title="生成 JSON" 
      cancelText=" "
      okText="确认"
      :destroyOnClose=true
      @ok="jsonShowOk"
    >
      <a-typography-paragraph :copyable="{ text: formDataJson }">
        {{ formDataJson }}
      </a-typography-paragraph>
    </a-modal>
    <!-- 导入 JSON 的提示框 -->
    <a-modal 
      v-model:visible="jsonInputVisible" 
      title="导入 JSON" 
      okText="确定导入"
      cancelText="取消"
      :destroyOnClose=true
      @ok="jsonInputOk"
      @cancel="jsonInputCancel"
    >
      <p> 请按照格式导入JSON </p>
      <a-textarea 
        v-model:value="jsonInputValue"  
        :autoSize="{minRows: 6}"
      />
    </a-modal>
    <!-- 发布表单的提示框 -->
    <a-modal 
      v-model:visible="publishVisible" 
      title="表单发布"
      okText="确定发布"
      cancelText="再考虑一下"
      :destroyOnClose=true
      @ok="publishConfirmOk"
    >
      <alert-outlined />
      <span>&nbsp; 确定发布表单吗</span>
    </a-modal>
    <!-- 表单预览框 -->
    <a-drawer
      :width="500"
      size="large"
      title="表单预览"
      placement="top"
      :visible="previewVisible"
      :destroyOnClose=true
      @close="previewClose"
    >
      <template #extra>
        <a-button type="primary" @click="previewClose">关闭预览</a-button>
      </template>
      <FormPreview :form-data-json="formDataJson" />
    </a-drawer>
  </a-layout>
  
</template>

<script setup>
import { computed, ref } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';
import draggable from "vuedraggable";
import { message } from 'ant-design-vue';
import { AlertOutlined }  from '@ant-design/icons-vue';
import FormGenerator from '@/components/Form/FormGenerator.vue';
import FormPreview from '@/components/Form/FormPreview.vue';
import { useSelectedComponentStore } from "@/stores/component";
import { useHttpUrl } from '@/stores/httpUrl';
import { nanoid } from 'nanoid';  

// 全局 URL 前缀
const httpUrl = useHttpUrl();

const router = useRouter();

// 表单标题
const formTitle = ref("表单标题");

// 组件列表 -> 存储在表单中的组件
const componentList = ref([]);

// 栈 -> 存储删除的组件
const stack = [];

// 选中的组件
const selected = ref({});
// 公共组件
const selectedComponent = useSelectedComponentStore();

// 表单 JSON 
const formDataJson = computed(() => {
  const formData = {
    title: formTitle.value,
    content: componentList.value,
  }
  return JSON.stringify(formData);
});

// 生成 JSON 提示框的显示情况
const jsonShowVisible = ref(false);
// 导入 JSON 提示框的显示情况
const jsonInputVisible = ref(false);
// 导入 JSON 提示框中的输入文本
const jsonInputValue = ref("");

// 预览框的显示情况
const previewVisible = ref(false);

// 发布表单提示框的显示情况
const publishVisible = ref(false);

// 选中组件
function select(element) {
  selected.value = element;
  selectedComponent.component = selected.value;
}

// 重置组件
function allReset() {
  selected.value = {};
  selectedComponent.component = {
    name: "",
    schema: "",
    field: "",
    refill: false,
    options: {
      type: "",
      cType: "",
      value: ""
    }
  };
}

// 清空表单
function allClear() {
  if(componentList.value.length > 0) {
    stack.push([...componentList.value]);
    componentList.value.length = 0;
    // 组件重置
    allReset();
  }
}

// 删除单个组件
function clear() {
  if(selected.value?.id) {
    stack.push(selected.value);
    for(let i = 0; i < componentList.value.length; i++) {
      if(selected.value.id == componentList.value[i].id) {
        componentList.value.splice(i, 1);
        break;
      }
    }
    // 组件重置
    allReset();
  }
}

// 撤销删除操作
function undo() {
  if(stack.length > 0) {
    componentList.value = componentList.value.concat(stack.pop());
  }
}

// 生成 JSON
function jsonShow() {
  jsonShowVisible.value = true;
}

// 生成 JSON 确定
function jsonShowOk(e) {
  jsonShowVisible.value = false;
}

// 导入 JSON
function jsonInput() {
  jsonInputVisible.value = true;
}

// 导入 JSON 确定
function jsonInputOk(e) {
  if(jsonInputValue.value.length > 0) {
    // 组件重置
    allReset();
    const formData = JSON.parse(jsonInputValue.value);
    formTitle.value = formData.title;
    componentList.value = formData.content;
    message.success('JSON 导入成功', 2);
  }
  jsonInputValue.value = "";
  jsonInputVisible.value = false;
}

// 导入 JSON 取消
function jsonInputCancel(e) {
  jsonInputValue.value = "";
  jsonInputVisible.value = false;
}

// 预览表单
function previewShow() {
  previewVisible.value = true;
}

// 关闭表单预览
function previewClose() {
  previewVisible.value = false;
}

// 发布表单确认
function publishConfirm() {
  publishVisible.value = true;
}

// 发布表单确定
function publishConfirmOk(e) {
  publishVisible.value = false;
  publish();
}

function publishCheck() {
  if(formTitle.value === "") {
    message.error("未输入表单标题");
    return false;
  }
  let count = 0;
  for(let val of componentList.value) {
    if(val.refill == true) {
      count++;
    }
  }
  if(count > 1) {
    message.error("回填字段过多");
    return false;
  }
  return true;
}

// 发布表单
function publish() {
  if(!publishCheck()) return;

  // 表单相应地址
  const subUrl = nanoid();
  
  // 地址
  // httpUrl.url 在 stores => httpUrl.js 中修改
  const url = httpUrl.url + "/user/submission/?subUrl=" + subUrl;
  
//   // 使用 axios 方式发送数据
//   axios.post('formUrl.do', {
//     url: url,
//     formData: formDataJson.value
//   }).then((result) => {
//       if(result.data === "Form released!") {
//         router.push({ name: 'formFinish', query: { subUrl: subUrl } });
//       }
//     }).catch((err) => {
//       console.log(err);
//     });
// }
  
  // 参数
  var params = new URLSearchParams();
  params.append("formData", formDataJson.value);
  params.append("url", url);

  // 使用 axios 方式发送数据
  axios.post('formUrl.do', params)
    .then((result) => {
      if(result.data === "Form released!") {
        router.push({ name: 'formFinish', query: { subUrl: subUrl } });
      }
    }).catch((err) => {
      console.log(err);
    });
}

</script>

<style scoped>
.container {
  height: 100%;
  /* 纵向滚动 */
  overflow-y: scroll;
}

.title {
  height: 40px;
  color: black;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  /* 为容器内首个元素和最后一个元素制造外边距 */
  margin: 4px 10px;
}

/* 组件靠左 */
.title-left {
  margin-right: auto;
}

/* 组件靠右 */
.title-right {
  margin-left: auto;
}

.form-title {
  text-align: center;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  width: 50%;
  border: 1px dashed rgba(55, 55, 55, 0.8);
}

/* 
 虚线的话使用 border-color 
 而不是 background-color 
*/
.divider {
  margin-top: 0;
  border-color: #1890ff;
}

/* 普通表单项 */
.item-content {
  margin-left: 10px;
  margin-right: 10px;
  border: 1px dashed rgba(55, 55, 55, 0.8);
}

/* 已选中表单项 */
.item-content.active {
  border: rgb(120, 200, 255) 1px solid;
}
</style>