<template>
  <a-layout class="container">
    <!-- 模块标题 -->
    <div class="title"><span> 属性配置 </span></div>
    <a-divider class="divider"></a-divider>
    
    <a-form layout="vertical">
      <a-form-item
        label="组件类型"
        class="attribute"
      >
        <a-input
          v-model:value="selectedComponent.component.options.cType"
          :disabled=true
        >
        </a-input>
      </a-form-item>
      <!-- 组件类型为文字 -->
      <template v-if="selectedComponent.component.options.type === 'text'">
        <a-form-item
          label="文本内容"
          class="attribute"
        >
          <a-textarea 
            v-model:value="selectedComponent.component.options.value"
            :autoSize="{minRows: 2}"
            :allowClear="true"
          >
          </a-textarea>
        </a-form-item>
        <a-form-item
          label="文字大小"
          class="attribute"
        >
          <a-input-number 
            v-model:value="selectedComponent.component.options.fontSize" 
            :min="1" 
            :max="20" 
          />
        </a-form-item>
        <a-form-item
          label="文本样式"
          class="attribute"
        >
          <a-radio-group v-model:value="selectedComponent.component.options.textType">
            <a-radio :style="radioStyle" value="">默认</a-radio>
            <a-radio :style="radioStyle" value="secondary">次要</a-radio>
            <a-radio :style="radioStyle" value="success">成功</a-radio>
            <a-radio :style="radioStyle" value="warning">警告</a-radio>
            <a-radio :style="radioStyle" value="danger">危险</a-radio>
          </a-radio-group>
        </a-form-item>
      </template>
      <!-- 组件类型为分割线 -->
      <template v-else-if="selectedComponent.component.options.type === 'divider'">
        <a-form-item
          label="分割线文字"
          class="attribute"
        >
          <a-textarea 
            v-model:value="selectedComponent.component.options.value"
            :autoSize="{minRows: 2}"
            :allowClear="true"
          >
          </a-textarea>
        </a-form-item>
        <a-form-item
          label="文字位置"
          class="attribute"
        >
          <a-radio-group v-model:value="selectedComponent.component.options.orientation">
            <a-radio :style="radioStyle" value="left">靠左对齐</a-radio>
            <a-radio :style="radioStyle" value="">居中对齐</a-radio>
            <a-radio :style="radioStyle" value="right">靠右对齐</a-radio>
          </a-radio-group>
        </a-form-item>
        <a-form-item
          label="分割线样式"
          class="attribute"
        >
          <a-radio-group v-model:value="selectedComponent.component.options.dashed">
            <a-radio :style="radioStyle" :value="true">虚线</a-radio>
            <a-radio :style="radioStyle" :value="false">实线</a-radio>
          </a-radio-group>
        </a-form-item>
      </template>
      <!-- 组件类型非文字或分割线 -->
      <template v-else>
        <a-form-item
          label="组件标题"
          class="attribute"
        >
          <a-input
            v-model:value="selectedComponent.component.name"
          >
          </a-input>
        </a-form-item>
        <a-form-item
          label="字段集合"
          class="attribute"
        >
          <a-select
            v-model:value="selectedComponent.component.schema"
            :options="schemas"
            @change="() => fieldClear()"
          ></a-select>
        </a-form-item>
        <a-form-item
          label="字段属性"
          class="attribute"
        >
          <a-select
            v-model:value="selectedComponent.component.field"
            :options="fields"
          ></a-select>
        </a-form-item>
        <!-- 组件类型为单行文本, 增加回填字段配置 -->
        <a-form-item
          v-if="selectedComponent.component.options.type === 'input'"
          label="设为回填字段"
          class="attribute"
        >
          <a-switch v-model:checked="selectedComponent.component.refill" />
          <exclamation-circle-two-tone
            title="一张表单仅含有一个回填字段"
            style="margin-left: 6px;"
          />        
        </a-form-item>
        <!-- 组件类型为单选或下拉选择, 增加选项卡配置 -->
        <a-form-item 
          v-if="selectedComponent.component.options.type === 'radio' || selectedComponent.component.options.type === 'select'"
          label="选项卡"
          class="attribute"
        >
          <a-button @click="radioAdd" size="small" style="margin-bottom: 4px;">
            添加选项
          </a-button>
          <template v-for="(item, index) in selectedComponent.component.options.valueData" :key="index">
            <a-input v-model:value="item.value">
              <template #addonAfter>
                <minus-circle-outlined @click="() => radioDelete(index)"/>
              </template>
            </a-input>
          </template>
        </a-form-item>
        <!-- 组件类型为评分, 增加限制值设置 -->
        <a-form-item 
          v-if="selectedComponent.component.options.type === 'rate'"
          label="总数"
          class="attribute"
        >
          <a-input-number 
            v-model:value="selectedComponent.component.options.limit" 
            :min="1" 
            :max="20" 
          />
        </a-form-item>
      </template>
    </a-form>

  </a-layout>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import axios from "axios";
import { useSelectedComponentStore } from "@/stores/component";
import { ExclamationCircleTwoTone, MinusCircleOutlined } from '@ant-design/icons-vue';

// 选中的组件
const selectedComponent = useSelectedComponentStore();

// 类型数据
const typeDatas = ref([]);

// 集合
const schemas = ref([]);

// 属性
const fields = computed(() => {
  return typeDatas.value.find(val => val.schemaName === selectedComponent.component.schema)?.fields.map(val => {
    return {
      value: val.fieldName,
      label: val.fieldName
    };
  });
});

// 属性重置
function fieldClear() {
  selectedComponent.component.field = "";
}

// 增加单选选项
function radioAdd() {
  selectedComponent.component.options.valueData.push({value: '新选项'});
}

// 删除单选选项
function radioDelete(index) {
  selectedComponent.component.options.valueData.splice(index, 1);
}

// 在组件挂载完成后执行
onMounted(() => {
  axios.get('getType.do')
    .then((result) => {
      typeDatas.value = result.data.map(val => {
        val.fields = val.fields.map(field => ({
          "fieldName": field
        }));
        return {
          "schemaName": val.schemaName,
          "fields": val.fields
        };
      });
      schemas.value = typeDatas.value.map(val => {
        return {
          value: val.schemaName,
          label: val.schemaName
        };
      })
    })
    .catch((error) => {
      console.log(error);
    })
});
</script>

<style scoped>
.container {
  height: 100%;
  overflow-y: auto;
}

.title {
  height: 40px;
  color: black;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.divider {
  margin-top: 0;
  border-color: #1890ff;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.attribute {
  width: 160px;
  margin: 10px;
}

input {
  border-radius: 5px;
}
</style>